body,
html,
a 
  {
    color: white;
    text-decoration: none;
    font-family: "Lato", sans-serif;
  }

  .card {
    background-color: rgba(74, 74, 74, 0.172);
    border-radius: 10px;
    border: 0.5px solid #ffffff71;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    max-width: calc(100% - 50rem);
    min-width: calc(100% - 20vw);
    margin: 10rem auto 0 auto;
  }

  .p-button {
    background-color: #000000;
    color: white;
    border: none;
    border-radius: 0.5rem;
    padding: 0.5rem 1rem;
    cursor: pointer;
    margin-top: 1rem;
  }

  footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 2.5rem;
    background-color: #00000097;
    text-align: center;
    padding: 1rem;
  }

  